import React from 'react'
import img from "./free-privacy.jpg"

function Privacy() {
    return (
        <>
            <section className='privacyMain'>
                <div className='privacyDiv'>
                    <div className="privacyImage">
                        <img src={img} alt="" />
                    </div>
                    <h2 className='Privacyheading'>Privacy Policy Page</h2>
                </div>
                <div className='boxHeading'>
                    <h5 style={{ margin: "2rem 0 1rem 0"}}> Everything you need to know about your personal data is here</h5>
                </div>

                <div className='paraContainer'>
                    <div>
                        <h4  style={{margin:"3rem 0"}}><b>Last Updated on 16/09/2023</b></h4>
                        <span  className="privacySpan">
                            Fluencer Digital, its subsidiaries and branch operations hereinafter referred as ‘Fluencer Digital’ ‘we’, ‘us’ or ‘our’ is committed to respect your privacy and choices. The privacy policy (hereinafter “Privacy Policy” or “Privacy”) highlights our privacy practices regarding Personal Information that we collect and process in compliance to applicable data privacy regulations.
                        </span>
                    </div>
                    <div>
                        <h4  style={{margin:"3rem 0"}}><b>Objective:</b></h4>
                       <span  className="privacySpan">
                            Through this privacy policy we intend to provide a synopsis of our privacy practices regarding Personal Information that we collect and process about you through various sources. Although the primary focus of this statement is on the data collected and processed through our Linkedin Lead Generation Pages, that link to this Privacy Statement.  Our adherence to the below mentioned principles remain across the organization towards personal data processing. Depending on the purposes of processing and your relationship with us as a data subject, we may provide additional customized privacy statements, more information on which can be found below.
                            <br /><br/>
                            The scope of this Statement covers the categories of personal data collected, how we use or process such data, who are the recipients of such data, and your associated rights under applicable laws including how to exercise the same. The applicability of the Privacy Statement is global, however certain additional information may be relevant to you depending on the country where you reside. Such additional terms, based on these particular countries or regions, are called out in region-specific statements below.
                        </span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 0rem 0"}}><b>1. Personal Information that we process and use</b></h3>
                        <br />
                        <h5 style={{ margin: "1rem 0 1rem 0"}}><b>1.1 Personal Information that we process </b></h5>
                        <span  className="privacySpan">We may collect the following categories of Personal Information:</span>
                        <br />
                        <span  className="privacySpan">
                            <ul>
                                <li style={{margin:"0.8rem 0"}}>Your Filled Personal and Contact details, such as, Name, title, e-mail address, phone numbers, address, designation, Company/Organization, Industry, Location (in terms of City/Country), LinkedIn profiles/URLs (or similar professional data set).</li>
                                <li style={{margin:"0.8rem 0"}}>Queries, comments, or feedback as submitted by you, including any correspondence you have made with us.</li>
                            </ul>
                        </span>
                    </div>

                    <div>
                        <h5 style={{ margin: "2rem 0 1rem 0"}}><b>1.2 Sources of Personal Information </b></h5>
                        <span  className="privacySpan">We may collect the aforementioned Personal Information through various sources, such as mentioned below:</span>
                        <br />
                        <span  className="privacySpan">
                            <ul>
                                <li style={{margin:"0.8rem 0"}}>Submitted by yourself, through our Linkedin forms, or by contacting/emailing our official contacts.</li>
                                <li style={{margin:"0.8rem 0"}}>Shared to Fluencer Digital employees, such as our sales or marketing representatives.</li>
                                <li style={{margin:"0.8rem 0"}}>Shared with or by Fluencer Digitals’ affiliates.</li>
                                <li style={{margin:"0.8rem 0"}}>Shared by our suppliers, vendors and service providers.</li>
                            </ul>
                        </span>
                    </div>

                    <div>
                        <h5 style={{ margin: "2rem 0 1rem 0"}}><b>1.3. Use of your Personal Information</b></h5>
                        <span  className="privacySpan">We may use your Personal Information for the following purposes:</span>
                        <ul>
                            <li style={{margin:"0.8rem 0"}}>to provide better usability, troubleshooting and maintenance</li>
                            <li style={{margin:"0.8rem 0"}}>to understand which parts of the website/pages/forms are visited and how frequently</li>
                            <li style={{margin:"0.8rem 0"}}>to facilitate communication with you, including contacting you and responding to your queries or requests</li>
                            <li style={{margin:"0.8rem 0"}}>To enable marketing and sales related communications and related purposes</li>
                            <li style={{margin:"0.8rem 0"}}>To know your interest of participation in ideathons or other competitions</li>
                            <li style={{margin:"0.8rem 0"}}>As a participant of any competitions and/or award ceremonies organized by us, to publish your name and corresponding details to make them available to larger audience on the Internet (including details of awards won); for the jury members and other associated delegates and speakers, to publish their name, title, photograph, and bio in a similar manner</li>
                            <li style={{margin:"0.8rem 0"}}>To invite you for events, seminars and equivalent ceremonies organized by us and related purposes such as running marketing or promotional campaigns, including such promotions or publications on social media</li>
                            <li style={{margin:"0.8rem 0"}}>To publish testimonials and case studies</li>
                            <li style={{margin:"0.8rem 0"}}>To generate and maintain leads as part of our Customer Relationship Management database</li>
                            <li style={{margin:"0.8rem 0"}}>To perform data analytics for providing a better user experience, enhance website performance and achieve business objectives</li>
                            <li style={{margin:"0.8rem 0"}}>To enhance and optimize our business operations, applications, systems, and processes, including operation and management of our communication assets and systems, ensuring and strengthening information security, maintenance of audit trail and associated records,</li>
                            <li style={{margin:"0.8rem 0"}}>To protect your data and Fluence Digital assets from information security threats and to secure against any unauthorized access, disclosure, alteration, or destruction</li>
                            <li style={{margin:"0.8rem 0"}}>To comply with applicable laws and other legal requirements, such as regulatory filings, record-keeping and reporting obligations, cooperating with government authorized inspections or requests, protecting our legal rights, and seeking remedies, including defending against any claims or engaging in legal proceedings, responding to subpoenas or court orders and managing documentation for related purposes</li>
                        </ul>
                    </div>

                    <div>
                        <h5 style={{ margin: "2rem 0 1rem 0"}}><b>1.4. Legal basis of the processing</b></h5>
                        <span  className="privacySpan">The applicable privacy and data protection laws provide for certain justifiable grounds for collection and processing of personal data, commonly referred to as legal basis of processing. We primarily rely on the following legal bases:</span>
                        <br /><br />
                        <span  className="privacySpan">1.4.1 We process your Personal Information when it is necessary for the performance of a contract to which you are the party or in order to take steps at your request prior to entering into a contract, e.g., when you would engage with us for receiving our services and offerings, or when managing the data of our employees for ensuring the performance of their employment contract.</span>
                        <br /><br />
                        <span  className="privacySpan">1.4.2 We process your Personal Information when it is necessary for the purposes of a legitimate interest pursued by us or a third party (when these interests are not overridden by your data protection rights), e.g., when we need to understand your usage of our website/pages/forms and interaction with the same, for generating your secure login credentials, or to optimize our processes.</span>
                        <br /><br />
                        <span  className="privacySpan">1.4.3 We process your Personal Information with your consent. Where we process Personal Data based on consent, your consent is revocable at any time, e.g., when registering and inviting you for events organized by us, or for sharing our marketing related communications with you.</span>
                        <br /><br />
                        <span  className="privacySpan">1.4.4 We may process your personal Information to comply with any Legal obligations on us, including to applicable laws and protecting our legal rights, seeking remedies, and defending against claims.</span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>2. Consequences of not providing Personal Information</b></h3>
                        <span  className="privacySpan">If you choose not to provide your Personal Information that is mandatory to process your request, we may not be able to fulfill the relevant purpose of processing, including provision of any services to you, if applicable. </span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 0rem 0"}}><b>3. Data recipients, transfer, and disclosure of Personal Information:</b></h3>
                        <br />
                        <div>
                            <h5 style={{ margin: "1rem 0 1rem 0"}}><b>3.1 Fluencer Digital does not share your Personal Information with third parties for their direct marketing purposes.</b></h5>
                            <span  className="privacySpan">We share your Personal Information within</span>
                            <ul>
                                <li style={{margin:"0.8rem 0"}}>Fluencer Digital or with any of its subsidiaries;</li>
                                <li style={{margin:"0.8rem 0"}}>Business partners;</li>
                                <li style={{margin:"0.8rem 0"}}>Service providers;</li>
                                <li style={{margin:"0.8rem 0"}}>Authorized third-party agents; or</li>
                                <li style={{margin:"0.8rem 0"}}>Auditors, and/or government authorities, where applicable.</li>
                            </ul>
                        </div>

                        <div>
                            <h5 style={{ margin: "2rem 0 1rem 0"}}><b>3.2 Facilitating International Data Transfers:</b></h5>
                            <span  className="privacySpan">We may transfer Personal Information to countries outside of your country of residence, either to Fluencer Digital group of companies or third parties(authorized), including to countries which have different data protection standards from those which apply in your country or region of residence. Our service providers are located globally; however, the primary locations are in USA, Canada, Australia, Singapore, Dubai, India, and UK.

                                Where we may transfer the data outside your country or region of residence, we shall take reasonable and appropriate steps in line with applicable laws while executing such transfer.

                                Please contact us as set out below if you would like to obtain a copy of the methods used when executing such international transfer as per applicable laws. When required, Fluencer Digital may disclose Personal Information to external law enforcement bodies or regulatory authorities, in order to comply with legal obligations.</span>
                        </div>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>4. Your Rights:</b></h3>
                        <span  className="privacySpan">4.1 Subject to the laws of your country, you may have certain rights as a data subject (including but not limited to right to information, access, rectification, erasure, object, restriction of processing, right to complain), relating to your Personal Information that we process. Where the data processing is based on your consent, you may withdraw your consent to the processing of any Personal Data at any time. This will not affect the lawfulness of any processing operation before your withdrawal.
                            <br /><br />
                            Your rights as a data subject may differ based on your applicable jurisdiction. Please refer to region-specific data privacy statements for more information on the same.</span>
                            <br/><br />
                            <span  className="privacySpan">4.2 Exercising your rights: For exercising your rights, we may need to request certain information from you for ascertaining your identity and confirming that you are entitled to exercise the same. You can exercise your rights by contacting us at <span style={{color:"#ff0000"}}>yash.kandoi@fluencer.co.in</span></span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>5. Data security</b></h3>
                        <span  className="privacySpan">At Fluencer Digital, there exists a perfect balance between Governance, Process and Technology, the combination of which has established Fluence Digitals' commitment to its customers and stakeholders. Fluence Digital adopts reasonable and appropriate security controls, practices and procedures including administrative, physical security, and technical controls in order to safeguard your Personal Information.</span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>6. Data retention</b></h3>
                        <span  className="privacySpan">Personal Information will not be retained for a period more than necessary to fulfill the purposes outlined in this privacy statement unless a longer retention period is required by law or for directly related legitimate business purposes. Personal Data that is no longer required to be retained as per legal and business requirements will be disposed in a secure manner. </span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>7. Contact your Local Data Protection Authority</b></h3>
                        <span  className="privacySpan">If you are unhappy with how we safeguard your personal data, depending on the laws of the countries where you reside, you have the right to bring a complaint to your local data protection authority. </span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>8. Linked websites</b></h3>
                        <span  className="privacySpan">Fluencer Digital provides links to third-party websites and services(authorized). However, Fluencer Digital is not responsible for the privacy statements, practices, or the contents of such third-party websites.</span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>9. Children’s Privacy</b></h3>
                        <span  className="privacySpan">Fluencer Digital Websites/pages/forms , its associated products or services and hosted contents, are not intended for the use by children.</span>
                        <br/>
                        <span  className="privacySpan">As such we do not knowingly solicit or collect personally identifiable information online from children without prior verifiable parental consent. If Fluencer Digital learns that a child has submitted personally identifiable information online without parental consent, it will take all reasonable measures to delete such information from its databases and to not use such information for any purpose (except where necessary to protect the safety of the child or others as required or allowed by law). If you become aware of any personally identifiable information we have collected from children, please email us at <span style={{color:'#ff0000'}}>yash.kandoi@fluencer.co.in</span></span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>10. How to contact us</b></h3>
                        <span  className="privacySpan">If you have any questions regarding our privacy practices or this privacy statement, or to request this privacy statement in another format, please contact us at:</span>
                        <br/>
                        <span  className="privacySpan">Contact person: YASH KANDOI</span>
                        <br/>
                        <span  className="privacySpan">Contact address: A-2/80 Chitrakoot Scheme, </span>
                        <br/>
                        <span  className="privacySpan">302021, Jaipur</span>
                        <br/>
                        <span  className="privacySpan">Phone: +91 83060 67708</span>
                        <br/>
                        <span  className="privacySpan">Email: <span style={{color:'#ff0000'}}>yash.kandoi@fluencer.co.in</span></span>
                    </div>

                    <div>
                       <h3 style={{ margin: "5rem 0 2rem 0"}}><b>11. Updates to this privacy statement</b></h3>
                        <span  className="privacySpan">
                        Fluencer Digital may change the data privacy practices and update this privacy statement as and when the need arises, and the same will be made available on the website. However, our commitment to protect the privacy of website users will continue to remain. 
                        </span>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Privacy
